import http from '../services/api/axios';

export default {
  state: () => ({
    userInfo: {
      username: '',
      role: '',
      full_name_position: '',
      region: '',
    },
    isAuth: false,
  }),
  getters: {
    getUserInfo(state) {
      return state.userInfo;
    },
    isAuth(state) {
      return state.isAuth;
    },
  },
  mutations: {
    setAuth(state, payload) {
      state.isAuth = payload;
    },
    setUserState(state, payload) {
      state.isAuth = true;
      state.userInfo = {
        ...payload,
        role: payload?.position,
      };
    },
  },
  actions: {
    setAuth({commit}, payload) {
      commit('setAuth', payload);
    },
    async me({commit}) {
      try {
        const resp = (
          await http.get('/auth/me', {
            params: {},
          })
        ).data;
        await commit('setUserState', resp);
      } catch (e) {
        // dispatch('errorHandler', e)
      }
    },
  },
};
