export default async function auth({to, from, store, next, nextMiddleware}) {
    const pathsForAdmin = ['add-worker-to-center', 'add-worker-to-center-monitoring', 'add-center', 'statistics', 'workers','index','centers','juveniles', 'all-donations', 'notification-detail', 'add-donations', 'teenager-info', 'print-teenager-info', 'monitoring-district-department', 'add-district-department', 'select-worker-position', 'teenager-info-print'];

    if (store.getters['isAuth'] === false) {
        await store.dispatch("me");
    }
    if (store.getters["getUserInfo"].role === "Apparat" && !pathsForAdmin.includes(to.name)) {
        console.log(pathsForAdmin.includes(to.name))
        next("/404");
    }
    return nextMiddleware();
}
