export default (el, _, vnode) => {
  var event = new Event('input', {bubbles: true});
  const regex = /[^\P{L}a-z][^a-z]*/giu;
  const value = el.value.replace(regex, '');
  el.value = value;
  vnode.el.dispatchEvent(event);
};
