export default {
    state: () => ({
        isSidebarOpen: false
    }),
    getters: {
        getIsSidebarOpened(state) {
            return state.isSidebarOpen;
        },
    },
    mutations: {
        setSidebarStatus(state, payload) {
            state.isSidebarOpen = payload;
        }
    },
    actions: {}
};
