export default async function isMonitoring({to, from, store, next, nextMiddleware}) {
  const pathsForAdmin = ['monitoring', 'juveniles'];

  if (store.getters['isAuth'] === false) {
    await store.dispatch('me');
  }
  if (store.getters['getUserInfo'].role !== 'Monitoring' && !pathsForAdmin.includes(to.name)) {
    next('/404');
  }
  return nextMiddleware();
}
